  background-color: var(--white);
  text-align: left;
  padding: 60px 80px 60px 60px;
  position: relative
}

.sg-section.header {
  border-bottom: 1px solid var(--accent-borders);
  background-color: var(--white)
}

.icon {
  background-color: var(--white);
