  border-radius: 10px;
  width: 55px;
  height: 55px;
  font-size: 18px;
  line-height: 100%
}

._2-1-column-grid {
  grid-column-gap: 24px;
  grid-row-gap: 24px;
  flex-direction: column;
  grid-template-rows: auto;
  grid-template-columns: 2fr 1fr;
  grid-auto-columns: 1fr;
  justify-content: center;
  align-items: stretch;
  width: 100%;
